.headerWrapper {
    @apply bg-gradient-to-br relative md:h-[40rem] xl:h-132 w-full px-4;
}

.headerContentWrapper {
    @apply flex items-center;
}

.headerLeftContent { 
    @apply flex flex-col items-center justify-self-start mx-auto w-45 ;
}

.headerLeftText {
    @apply relative uppercase font-bold w-full text-left;
}

.headerRightContent {
    @apply relative mx-auto justify-center xl:w-45;
}


.headerRightRecContent {
    @apply -rotate-45 flex flex-col;
}

.image {
    background-image: url('../../resources/images/person.png');
    clip-path: url(#myClip);
    /* height: 300px; */
    /* width: 100%; */
}


/* Bottom Curve */
.headerBottomCurve {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
    line-height: 0;
}